<template>
  <div class="wrapper" ref="wrapper">
    <div class="content">
      <slot></slot>
    </div>
  </div>
</template>

<script>
import BScroll from "better-scroll";
export default {
  props: {
    probeType: {
      type: Number,
      default: 0,
    },
    pullUpLoad: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      scroll: null,
      message: "hihihi",
    };
  },
  methods: {
    // 
    scrollTo(x, y, time = 300) {
      this.scroll?.scrollTo(x, y, time);
    },
    // 
    finishPullUp() {
      this.scroll?.finishPullUp();
    },
    // 
    refresh() {
      this.scroll?.refresh();
    },
    getScrollY() {
      return this.scroll ? this.scroll.y : 0;
    },
  },
  mounted() {
    // 
    this.scroll = new BScroll(this.$refs.wrapper, {
      click: true,
      probeType: this.probeType,
      pullUpLoad: this.pullUpLoad,
    });
    // 
    this.scroll.on("scroll", (e) => {
      this.$emit("scroll", e);
    });
    // 
    if (this.pullUpLoad) {
      this.scroll.on("pullingUp", () => {
        this.$emit("pullingUp");
        console.log('')
      });
    }
  },
};
</script>

